    computed: {
		app() { return this.$store.getters.app },
		user() { return this.$store.getters.user },
		product() { return this.$store.getters.product },
        productId() { return this.$route.params.productId },
        
        currentProduct() {
            let currentProduct = null;
            this.product.list.forEach(p => {
                if(p.id == this.productId) {
                    currentProduct = p;
                }
            })

            for(const key in this.product.balance) {
                if(key == this.productId) {
                    currentProduct.money = this.product.balance[key].money;
                }
            }

            return currentProduct;
        },

        currentNav() {
            let res = [];

            this.product.navList.forEach(nav => {
                nav.list && nav.list.forEach(item => {
                    if(item.id == this.productId) {
                        res = nav.list;
                    } 
                })
            })
            
            return res.filter(item => {
                return item.is_show == 'Y';
            });
        },
        
        filterProduct() {
            const checkNavIsShow = (id) => {
                let res = false;
                this.product.navList.forEach(nav => {
                    nav.list && nav.list.forEach(p => {
                        if(p.id == id) {
                            if(nav.is_show == 'Y') {
                                res = true;
                            } 
                        }
                    })
                })

                return res;
            }

            const list = this.product.list.filter(item => {
                return item.is_show == 'Y' && checkNavIsShow(item.id);
            });

            return list;
        }
    },

    mounted() {
        this.init()
    },

    methods: {
        init() {
            this.currentProductId = this.productId;

            if(this.user.roles.indexOf('user') > -1) {
                if([10, 5, 18].indexOf(Number(this.productId)) > -1) {
                    return ;
                }

                this.updateFrameHeight();
                if(this.app.autoExchange) {
                    this.reloadBalance((hadExchange) => {
                        if(hadExchange) {
                            const product_ids = this.filterProduct.map(item => { return item.id }).join(',');
                            this.$store.dispatch('productBalanceByAjax', { product_ids });
                        }
                        
                        this.getProductUrl();
                        this.initTimer();
                    });
                }else {
                    this.getProductUrl();
                    this.initTimer();
                }

            }else {
                if(this.user.roles.indexOf('try') > -1) {
                    return ;
                }
                
                this.updateFrameHeight();
            }
        },

        initTimer() {
            clearInterval(this.timer);

            const run = () => {
                this.runBalaceSync();
            }
            
            this.timer = setInterval(run, 30000);

            setTimeout(() => {
                run();
            }, 1000);
        },

        reloadBalance(callback) {
            const product_ids = this.filterProduct.map(item => { return item.id }).join(',');
            this.loading = true;
            this.$store.dispatch('productBalanceByAjax', { product_ids }).then((json) => {
                if(json.code == 200) {
                    let idArr = [], total_money = 0;
                    for(let id in json.list) {
                        const money = Number(json.list[id].money);
                        
                        if(id != 0 && id != this.productId) {
                            // 大于 0 则 回收
                            if(money > 0) {
                                idArr.push(id);
                            }
                        }

                        if(id == 0) {
                            total_money = money
                        }
                    }

                    if(idArr.length) {
                        const data = {
                            type: 3,
                            product_id: idArr.join(','),
                        }

                        transfer(data).then(json => {
                            if (json.code == 200) {
                                transferallout(this.productId).then(() => {
                                    setTimeout(() => {
                                        this.loading = false;
                                        callback && callback(1);
                                    }, 3000);     
                                })
                            } else {
                                this.$message.error(json.info);
                                this.loading = false;
                                callback && callback();
                            }
                        });
                    }else {
                        if(total_money > 0) {
                            transferallout(this.productId).then(() => {
                                this.loading = false;
                                callback && callback();
                            })
                        }else {
                            this.loading = false;
                            callback && callback();
                        }
                    }

                }else {
                    this.$message.error(json.info);
                    this.loading = false;
                    callback && callback();
                }
            });
        },

        runBalaceSync(cb) {
            this.$store.dispatch('productBalanceSync', { product_ids: this.productId }).then(() => {
                cb && cb()
            });
        },

        KGClick(gameType) {
            this.$scrollTo(this.$el.querySelector('.p_container'), 100)

            this.gameType = gameType;
            this.updateFrameHeight();
            this.getProductUrl();
        },

        getProductUrl(callback) {
            this.loading = true;
            const data = {
                product_id: this.productId,
                game_type: this.gameType,
                t: +new Date(),
            };

            getUrl(data).then(json => {
                if (json.code == 200) {
                    this.originUrl = json.list.login_url.replace('http://', '//');
                    this.currentUrl = this.originUrl;
                } else {
                    this.loading = false;
                    this.hasError = true;
                }

                this.$nextTick(() => {
                    callback && callback();
                    // this.loading = false;
                })
            });
        },

        load() {
            this.loading = false;
        },

        login() {
            this.$store.dispatch('openLoginDialog');
        },

        submitForm() {
            if(this.submitLoading) return ;

            this.$refs.form.validate((valid) => {
                if (valid) {
                    const data = Object.assign({}, this.formData);
                    data.product_id = this.productId;

                    data.amount = Number(data.amount);

                    const compareMoney = data.type == 2 ? this.getMoney(0) : this.getMoney(this.productId);
                    if(data.amount > compareMoney) {
                        this.$message.error('余额不足');
                        return ;
                    }

                    this.submitLoading = true;
                    transfer(data).then(json => {
                        this.submitLoading = false;
                        
                        if (json.code == 200) {
                            this.$message({
                                message: '操作成功，转账已受理，请稍后查看金额',
                                type: 'success',
                                duration: 3000,
                            });
                            
                            setTimeout(() => {
                                this.runBalaceSync();
                                this.getProductUrl();

                                setTimeout(() => {
                                    this.runBalaceSync();
                                }, 1000)
                            }, 1500);
                            
                            this.dialogVisible = false;
                        } else {
                            this.$message.error(json.info);
                        }
                    });
                }
            });
        },

        updateMoney(data, productId) {
            if(data.total_money !== undefined) {
                this.$store.dispatch('updateUserInfo', { total_money: data.total_money })
            }

            if(data.product_money !== undefined) {
                this.$store.dispatch('setBalanceById', { id: productId, money: data.product_money })
            }
        },

        openForm(type) {
            this.formData.type = type;
            this.formData.amount = '';

            if(type == 3) {
                if(this.moneyReturnLoading) return ;

                if(this.getMoney(this.productId) == 0) {
                    this.$message.info('游戏余额为空，不需要全部转出');
                    return ;
                }
                this.$confirm('确认全部转出吗?', '提示', {
                    type: 'warning'
                }).then(() => {
                    const data = {
                        type,
                        product_id: this.productId,
                    }

                    this.moneyReturnLoading = true;
                    transfer(data).then(json => {
                        this.moneyReturnLoading = false;
                        if (json.code == 200) {
                            this.$message({
                                message: '操作成功，转账已受理，请稍后查看金额',
                                type: 'success',
                                duration: 3000,
                            });
                            
                            setTimeout(() => {
                                this.runBalaceSync();
                                this.getProductUrl();

                                setTimeout(() => {
                                    this.runBalaceSync();
                                }, 1000)
                            }, 1500);
                        } else {
                            this.$message.error(json.info);
                        }
                    });
                }).catch(() => {
                    
                });
                return ;
            }

            const productMoneyText = this.currentProduct.name + ' <span class="money">￥'+ this.getMoney(this.productId) +'</span>';
            const moneyText = '钱包余额 <span class="money">￥'+ this.getMoney(0) +'</span>';

            if(type == 2) {
                this.toText = productMoneyText;
                this.fromText = moneyText;
            }else {
                this.fromText = productMoneyText;
                this.toText = moneyText;
            }

            this.dialogVisible = true;
        },

        getMoney(id) {
            if(id == 0) return Number(this.user.userInfo.total_money);
            
            for(const key in this.product.balance) {
                if(key == id) {
                    return Number(this.product.balance[key].money);
                }
            }

            return 0;
        },

        _btnReload() {
            if(this.money_btning) return ;

            this.money_btning = true;
            // const product_ids = [this.productId].join(',');

            this.runBalaceSync(() => {
                this.money_btning = false;
            })

            // this.$store.dispatch('productBalanceSync', { product_ids }).then(() => {
            //     this.money_btning = false;
            // }); 
        },

        _btnUrl() {
                this.currentUrl = this.originUrl + '&t=' + (+new Date());
        },

        updateFrameHeight() {
            if(this.screenFull) {
                const wH = window.innerHeight;
                if(this.frameHeight != wH - 80) {
                    this.frameHeight = wH - 80;
                }
            }else {
                const pW = document.getElementById('product_url').offsetWidth;
                if(this.frameHeight != pW * 9 / 18) {
                    this.frameHeight = pW * 9 / 18;
                }
            }
        },

        onScreenFull(e) {
            if(this.screenFull) {
                this.getProductUrl();
                e.preventDefault();
                e.stopPropagation();
            }else {
                this.currentUrl = '';
            }

            this.screenFull = !this.screenFull;

            // this.updateFrameHeight();

            // this.$nextTick(() => {
            //     this.getProductUrl();
            // })
        },
    },

    beforeDestroy() {
        clearInterval(this.timer);
    },
}
</script>

